html,body{
    height: 100vh;
    width: 100%;
}

.calendar-screen{
    display: flex;
    flex-flow: column;
    height: 100vh;
}

.rbc-calendar {
    height: 100vh;
}

.login-container{
    margin-top: 15%;
}
.login-form-1{
    border-radius: 5px;
    box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 9px 26px 0 rgba(0, 0, 0, 0.19);
    padding: 5%;
}
.login-form-1 h3{
    text-align: center;
    color: #333;
}
.login-form-2{
    border-radius: 5px;
    box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 9px 26px 0 rgba(0, 0, 0, 0.19);
    padding: 5%;
    background: #0062cc;
}
.login-form-2 h3{
    text-align: center;
    color: #fff;
}
.login-container form{
    padding: 10%;
}
.btnSubmit
{
    width: 50%;
    border-radius: 1rem;
    padding: 1.5%;
    border: none;
    cursor: pointer;
}
.login-form-1 .btnSubmit{
    color: #fff;
    background-color: #0062cc;
}
.login-form-2 .btnSubmit{
    background-color: #fff;
    color: #0062cc;
}
.login-form-2 .ForgetPwd{
    color: #fff;
    font-weight: 600;
    text-decoration: none;
}
.login-form-1 .ForgetPwd{
    color: #0062cc;
    font-weight: 600;
    text-decoration: none;
}

.ReactModalPortal > div{
    opacity: 0;
  }
  
  .ReactModalPortal .ReactModal__Overlay {
    align-items: center;
    display: flex;
    justify-content: center;
    transition: opacity .2s ease-in-out;
    z-index: 999;
  }
  
  .modal-fondo {
    background-color: rgba(0, 0, 0, 0.3);
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    position: fixed;
  }
  
  .ReactModalPortal .ReactModal__Overlay--after-open {
    opacity: 1;
  }
  
  .ReactModalPortal .ReactModal__Overlay--before-close {
    opacity: 0;
  }
  
  .modal {
    background: white;
    border-radius: 5px;
    color: rgb(51, 51, 51);
    display: inline;
    max-height: 560px;
    max-width: 500px;
    outline: none;
    padding: 10px;
  }

  .react-datetime-picker input:focus{
    outline: none;

}

.react-datetime-picker__wrapper{
 display: flex;
 border: none;

}

.agregar{
    background-color: blue;
    /* width: 20%; */
    position: fixed;
    bottom: 5%;
    right: 5%;
    font-size: 3.5rem;
    color: white;
    border: none;
    border-radius: 50%;
    padding: 0rem 1.5rem;
    cursor: pointer;
}
.agregar:hover{
    background-color: rgb(24, 9, 94);
    transform: scale(1.1);
}

.borrar{
    background-color: red;
    /* width: 20%; */
    position: fixed;
    bottom: 5%;
    left: 5%;
    font-size: 3rem;
    color: white;
    border: none;
    border-radius: 50%;
    padding: 0rem 1.5rem;
    cursor: pointer;
    z-index: 100;
}
.borrar:hover{
    background-color: rgb(116, 7, 7);
    transform: scale(1.1);
}